import React, { Component } from 'react';
import Typing from 'react-typing-animation'
// import Input from './input';

const languages = "python, java, c++/c, c#, ruby"
const web = "node, express, react, angular, rails"
const other = "qt, opencv, tensorflow, tesseract, mongodb/sql"

class Text extends Component {
  constructor(props){
    super(props)
    this.state = {
      txt: props.txt,
      speed: props.speed,
      delay: props.delay,
      id: props.id
    }
    this.renderText = this.renderText.bind(this);
  }

  componentWillMount() {
    if (JSON.parse(localStorage.getItem('visited')) == true) {
      this.setState({delay: 0, speed: 0.1})
    }
  }

  renderText() {
    return(
      this.state.txt.map((t)=><span>{t}<br/></span>)
    );
  }
  render() {
    let textOutput;
    if (typeof this.state.txt === "object") {
      textOutput = this.renderText();
    } else { textOutput = this.state.txt }
    return(
      <div>
      <Typing
          speed={this.state.speed}>
          <Typing.Delay ms={this.state.delay}/>
        <span id={this.state.id}>{textOutput}</span>
      </Typing>
      </div>
    )
  }
}

export default class Term extends Component {
  constructor(props) {
    super(props)
    this.state = {visited: false}
  }
  componentDidMount() {
    if (localStorage.getItem('visited') != "true") {
      localStorage.setItem('visited', JSON.stringify(true))
    }
  }
  render() {
    return (
      <div className="terminal">
      <div className="terminal-header">
      <span id="header-text">tollef-jorgensen@homepage</span>
      <span id="header-btn">&times;</span>
      <hr/>
      </div>
      <div className="terminal-content">
      <Text id="info-text" txt="ls" speed="50" delay="0" />
      <Text txt={["known-languages.txt","some-web-stuff.txt", "other-things.txt"]} speed="10" delay="400" />
      <Text id="info-text" txt="cat known-languages.txt" speed="20" delay="2000"/>
      <Text txt={languages} speed="15" delay="3000" />
      <Text id="info-text" txt="cat some-web-stuff.txt" speed="20" delay="5000"/>
      <Text txt={web} speed="15" delay="6000" />
      <Text id="info-text" txt="cat other-things.txt" speed="20" delay="8000"/>
      <Text txt={other} speed="15" delay="9000" />
      </div>
      </div>
    );
  }
}



